___CSS_LOADER_EXPORT___.push([module.id, `html, body {
    font-size: 62.5% ;
    background-color: #f1f0f0;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    min-height: 100vh;
    gap: 4rem;
}     
/* Контейнер прогресс-бара */
.progress-ring-container {
    display: flex;
    justify-content: space-between;
}
/* Панель управления */
.toggle-bar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 2rem;
}
/* Прогресс-бар */
.progress-ring__circle {
    transform-origin: center;
    transform: rotate(-90deg);
    transition: stroke-dashoffset .1s;
}

/* Прогресс-бар - анимация */
.progress-ring {
    transition: opacity 0.3s ease-out;
}

.progress-ring__circle::before {
    transform-origin: center;
    transform: rotate(-90deg);
    transition: stroke-dashoffset 0.3s;
}

.progress-ring__circle::after {
    content: "";
    position: absolute;
    inset: 0;
    background-color: blue;
    opacity: 0;
}

/* Поле ввода процентов */
.percent {
    display: flex;
    text-align: center;
    width: 4.7rem;
    height: 2.7rem;
    border-radius: 10rem;
    border: 1px solid #74777a; 
    -moz-appearance: textfield;
    appearance: textfield;
}
/* Название поля ввода процентов */
.toggle-bar__label {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    font-size: 1.5rem;
}

/* ПЕРЕМЕННЫЕ / параметры трека */
.gui-switch {
    --track-size: 5.1rem;
    --track-inactive: #dee5ea;
    --track-active: blue;
    --track-color-inactive: var(--track-inactive);
    --track-color-active: var(--track-active);
    --track-padding: .2rem;

    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    font-size: 1.5rem;
}

/* Ползунок */
.gui-switch > input {
    --thumb-size: 2.7rem;
    --thumb-position: 0; 

    appearance: none;
    border: none;
    padding: .2rem;
    outline-offset: .5rem;
    box-sizing: content-box;
    inline-size: var(--track-size);
    block-size: var(--thumb-size);
    background-color: #dee5ea;
    border-radius: 10rem;
    flex-shrink: 0;
    display: grid;
    align-items: center;
    grid: [track] 1fr / [track] 1fr;
    margin: 0;
}


/* Стили для сенсорного экрана */
.gui-switch {
    cursor: pointer;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}

/*  Ползунок - начальное состояние */
.gui-switch > input::before {   
    content: "";
    border: none;
    grid-area: track;
    inline-size:  var(--thumb-size);
    block-size: var(--thumb-size);
    background-color: #ffffff;
    transition: .4s;
    border-radius: 10rem;
    transform: translateX(var(--thumb-position));
    box-shadow: 0 0 .1rem 0 rgba(0, 0, 0, 0.5);
}

/* Ползунок - состояние "включено" */
.gui-switch > input:checked {
    --thumb-position: calc(var(--track-size) - 100%);
    background-color: blue;
}

/* Убираем стрелочки у input[type="number"] в WebKit браузерах */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Убираем стрелочки у input[type="number"] в Firefox */
input[type="number"] {
    -moz-appearance: textfield;
    appearance: textfield;
}

@media (max-width: 568px) {
  .container {
    gap: 4rem;
    padding: 1rem;
  }

  .toggle-bar {
    gap: 1rem;
  }

  .toggle-bar__label {
    font-size: 1.2rem;
  }
  
  .gui-switch {
    font-size: 1.2rem;
  } 
}

@media (max-width: 480px) {
    .container {
        gap: 2rem;
    }
    
    
}       

@media (max-width: 320px) {
    .container {
        flex-direction: column;
        gap: 4rem;
    }

    .toggle-bar {   
        gap: 1rem;
    }

    .toggle-bar__label {
        font-size: 1.2rem;
    }
    
}`, "",{"version":3,"sources":["webpack://./project-root/style.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;IAClB,iBAAiB;IACjB,SAAS;AACb;AACA,4BAA4B;AAC5B;IACI,aAAa;IACb,8BAA8B;AAClC;AACA,sBAAsB;AACtB;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;AACb;AACA,iBAAiB;AACjB;IACI,wBAAwB;IACxB,yBAAyB;IACzB,iCAAiC;AACrC;;AAEA,4BAA4B;AAC5B;IACI,iCAAiC;AACrC;;AAEA;IACI,wBAAwB;IACxB,yBAAyB;IACzB,kCAAkC;AACtC;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,QAAQ;IACR,sBAAsB;IACtB,UAAU;AACd;;AAEA,yBAAyB;AACzB;IACI,aAAa;IACb,kBAAkB;IAClB,aAAa;IACb,cAAc;IACd,oBAAoB;IACpB,yBAAyB;IACzB,0BAA0B;IAC1B,qBAAqB;AACzB;AACA,kCAAkC;AAClC;IACI,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,iBAAiB;AACrB;;AAEA,iCAAiC;AACjC;IACI,oBAAoB;IACpB,yBAAyB;IACzB,oBAAoB;IACpB,6CAA6C;IAC7C,yCAAyC;IACzC,sBAAsB;;IAEtB,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,iBAAiB;AACrB;;AAEA,aAAa;AACb;IACI,oBAAoB;IACpB,mBAAmB;;IAEnB,gBAAgB;IAChB,YAAY;IACZ,cAAc;IACd,qBAAqB;IACrB,uBAAuB;IACvB,8BAA8B;IAC9B,6BAA6B;IAC7B,yBAAyB;IACzB,oBAAoB;IACpB,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,+BAA+B;IAC/B,SAAS;AACb;;;AAGA,gCAAgC;AAChC;IACI,eAAe;IACf,iBAAiB;IACjB,wCAAwC;AAC5C;;AAEA,oCAAoC;AACpC;IACI,WAAW;IACX,YAAY;IACZ,gBAAgB;IAChB,+BAA+B;IAC/B,6BAA6B;IAC7B,yBAAyB;IACzB,eAAe;IACf,oBAAoB;IACpB,4CAA4C;IAC5C,0CAA0C;AAC9C;;AAEA,oCAAoC;AACpC;IACI,gDAAgD;IAChD,sBAAsB;AAC1B;;AAEA,gEAAgE;AAChE;;IAEI,wBAAwB;IACxB,SAAS;AACb;;AAEA,uDAAuD;AACvD;IACI,0BAA0B;IAC1B,qBAAqB;AACzB;;AAEA;EACE;IACE,SAAS;IACT,aAAa;EACf;;EAEA;IACE,SAAS;EACX;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;AACF;;AAEA;IACI;QACI,SAAS;IACb;;;AAGJ;;AAEA;IACI;QACI,sBAAsB;QACtB,SAAS;IACb;;IAEA;QACI,SAAS;IACb;;IAEA;QACI,iBAAiB;IACrB;;AAEJ","sourcesContent":["html, body {\n    font-size: 62.5% ;\n    background-color: #f1f0f0;\n}\n\n.container {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    position: relative;\n    min-height: 100vh;\n    gap: 4rem;\n}     \n/* Контей